@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
:root {
  --Very-Dark-Gray: hsl(0, 0%, 17%);
  --Dark-Gray: hsl(0, 0%, 59%);
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  min-height: 100vh;
  font-family: "Rubik", sans-serif;
  font-size: 1.8rem;
}
h1 {
  color: white;
  font-weight: 500;
  font-size: 3.3rem;
}
.App {
  display: flex;
  flex-direction: column;
}
.App-header {
  background-image: url(/ip-address-tracker/static/media/pattern-bg.a60a3c85.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left;
  height: 35vh;
  padding-top: 2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 4rem;
  gap: 4rem;
  position: relative;
}
.map-container {
  position: relative;
  height: 65vh;
}
.map {
  height: 100%;
  pointer-events: none;
}

.marker {
  position: absolute;
  left: 50%;
  z-index: 10;
  top: 40%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.form {
  display: flex;
  width: 40vw;
  min-width: 40rem;
  height: 40rem;
}
.ip-input {
  flex-grow: 1;
  border: none;
  font-size: 1.8rem;
  outline: none;
  padding-inline: 2rem;
  border-radius: 1rem 0 0 1rem;
  cursor: pointer;
}
.ip-input::-webkit-input-placeholder {
  color: hsl(0, 0%, 59%);
  color: var(--Dark-Gray);
}
.ip-input:-ms-input-placeholder {
  color: hsl(0, 0%, 59%);
  color: var(--Dark-Gray);
}
.ip-input::placeholder {
  color: hsl(0, 0%, 59%);
  color: var(--Dark-Gray);
}
.submit-btn {
  outline: none;
  border: none;
  padding: 2rem 2.5rem;
  padding-bottom: 1.8rem;
  background-color: black;
  border-radius: 0 1rem 1rem 0;
  background-image: url(/ip-address-tracker/static/media/icon-arrow.2eaaa5ee.svg);
  background-position: center;
  background-size: auto;
  color: transparent;
  background-repeat: no-repeat;
}
.submit-btn:hover {
  cursor: pointer;
  background-color: hsl(0, 0%, 17%);
  background-color: var(--Very-Dark-Gray);
}

.output-box {
  bottom: 0;
  z-index: 10;
  background: white;
  display: flex;
  padding: 4rem;
  grid-gap: 2rem;
  border-radius: 1rem;
  box-shadow: 0 4rem 2rem -3rem rgba(0, 0, 0, 0.3);
}
.output-blocks {
  display: grid;
  grid-gap: 1rem;
  align-content: start;
  padding-right: 3rem;
}
.loader {
  position: absolute;
  z-index: 20;
  background-color: rgba(255, 255, 255, 0.5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader::after {
  content: "";
  height: 5rem;
  width: 5rem;
  position: absolute;
  border-radius: 50%;
  border: 5px solid hsl(0, 0%, 17%);
  border: 5px solid var(--Very-Dark-Gray);
  border-bottom: 5px solid rgb(0, 204, 255);
  -webkit-animation: loading 1s linear infinite;
          animation: loading 1s linear infinite;
}
@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.label {
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 500;
  color: hsl(0, 0%, 59%);
  color: var(--Dark-Gray);
}
.value {
  font-size: 2.2rem;
  font-weight: 700;
  color: hsl(0, 0%, 17%);
  color: var(--Very-Dark-Gray);
}
.line {
  background-color: hsl(0, 0%, 59%);
  background-color: var(--Dark-Gray);
  width: 1px;
  height: 80%;
  align-self: center;
}
.attribution {
  position: fixed;
  bottom: 0.5rem;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: -webkit-max-content;
  width: max-content;
}
.attribution a {
  text-decoration: none;
  color: rgb(0, 136, 255);
}
@media screen and (max-width: 1000px) {
  .output-box {
    flex-direction: column;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    -webkit-transform: scale(0.8) translateY(-20%);
            transform: scale(0.8) translateY(-20%);
  }
  .form {
    min-width: 30rem;
    justify-self: center;
    margin: 0 auto;
  }
  .ip-input {
    font-size: 1.2rem;
  }
  .line {
    display: none;
  }
  .output-blocks {
    padding-right: 0;
    grid-gap: 0.1rem;
  }
  .marker {
    top: 60%;
  }
}

